<template>
    <div>
        <h1>{{ message }}</h1>
    </div>
</template>

<script>
import axios from "axios";

export default {
    data() {
        return {
            message: "",
        };
    },
    mounted() {
        axios.get("http://localhost:8089/api/hello").then((res) => {
            this.message = res.data;
        });
    },
};
</script>